<script>
import $ from "jquery";
export default {
  name: "articleDetail",
  components: {},
  data() {
    return {
      id: "",
      bean: {},
    };
  },
  created() {
    const url = new URL(location.href);
    this.id = url.searchParams.get("id");
    console.log(this.id);
    $.ajax({
      url: "/topic/backstage/articleDetail/" + this.id,
      type: "POST",
      async: false, //同步請求
      cache: false, //不快取頁面
      success: (response) => {
        this.bean = response.article;
        this.bean.content = response.article_content.content;
        console.log(response);
      },
      error: function (returndata) {
        console.log(returndata);
      },
    });
  },
  methods: {
    changeState(s) {
      $.ajax({
        url: "/topic/backstage/changeState/" + s + "/" + this.id,
        type: "POST",
        async: false, //同步請求
        cache: false, //不快取頁面
        success: (response) => {
          this.bean.state = s;
          if (response) {
            this.$message(s + "成功");
          } else {
            this.$message.error("修改失敗");
          }
        },
        error: function (returndata) {
          console.log(returndata);
        },
      });
    },
    replyState(s, state) {
      $.ajax({
        url: "/topic/backstage/replyState/" + state + "/" + s.replyid,
        type: "POST",
        async: false, //同步請求
        cache: false, //不快取頁面
        success: (response) => {
          s.state = state;
          if (response) {
            this.$message(state + "成功");
          } else {
            this.$message.error("修改失敗");
          }
        },
        error: function (returndata) {
          console.log(returndata);
        },
      });
    },
    goBack() {
      this.$router.back();
    
    },
  },
};
</script>

