<template>
  <div>
    <el-page-header @back="goBack" content="客戶細節"> </el-page-header>
    <p>會員名稱</p>
    <p>{{ bean.name }}&nbsp;</p>
    <p>Email</p>
    <p>{{ bean.email }}&nbsp;</p>
    <p>組資單位</p>
    <p>{{ bean.company }}&nbsp;</p>
    <p>聯絡電話</p>
    <p>{{ bean.phone }}&nbsp;</p>
    <p>聯絡地址</p>
    <p>{{ bean.address }}&nbsp;</p>
    <p>生日</p>
    <p>{{ bean.birthday }}&nbsp;</p>
    <p>積分</p>
    <p>{{ bean.integral }}&nbsp;</p>
    <br />
    <el-switch
      @change="permit"
      v-model="VIP"
      active-text="VIP"
      inactive-text="取消"
    >
    </el-switch
    ><br /><br />
    <el-switch
      @change="permit"
      v-model="manage"
      active-text="管理"
      inactive-text="取消"
    >
    </el-switch>
  </div>
</template>

<script>
import $ from "jquery";
export default {
  name: "memberDetail",
  components: {},
  data() {
    return {
      checkboxGroup: ["管理"],
      bean: {},
      VIP: false,
      manage: false,
    };
  },
  created() {
    const url = new URL(location.href);
    const id = url.searchParams.get("id");
    $.ajax({
      url: "/topic/backstage/detail/" + id,
      type: "POST",
      async: false, //同步請求
      cache: false, //不快取頁面
      success: (response) => {
        this.bean = response;
        this.bean.permitList.forEach((e) => {
          if (e.level == 8) {
            this.VIP = true;
            return;
          }
          if (e.level == 9) this.manage = true;
        });
      },
      error: function (returndata) {
        console.log(returndata);
      },
    });
  },
  methods: {
    goBack() {
      this.$router.push("/topic/memberList");
    },
    permit() {
      $.ajax({
        url:
          "/topic/backstage/changePermit/" +
          this.bean.memberid +
          "?VIP=" +
          this.VIP +
          "&manage=" +
          this.manage,
        type: "POST",
        async: false, //同步請求
        cache: false, //不快取頁面
        success: (response) => {
          console.log(response);
        },
        error: function (returndata) {
          console.log(returndata);
        },
      });
    },
  },
};
</script>

